import React, { Component } from 'react';
import { connect } from "react-redux";
import { toast } from "react-toastify";
import { Modal } from 'react-bootstrap';
import { createCollection } from '../../../../Services/Actions/homeActions'
import { getCollectionList } from '../../../../Services/Actions/homeActions';
class CreateCollectionModal extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        collectioName: null,
        isRequesting: false
    };

    onClickCreate = event => this.onEventCreate()


    onEnterCreate = event => {
        if (event.key === "Enter") this.onEventCreate()
    }
    onEventCreate = () => {
        var self = this
        const { collectionData } = this.props
        const { collectioName } = this.state;
        this.setState({ isRequesting: true })
        if (collectioName && String(collectioName).length > 0) {
            var toastID = toast.loading("Loading...")
            if (collectionData && Object.keys(collectionData).length > 0 && collectionData.collectionList && collectionData.collectionList.length > 0) {
                const existsCollection = collectionData.collectionList.filter(collection => String(collection.name).toLowerCase() == String(collectioName).toLowerCase());
                if (existsCollection && existsCollection.length > 0) {
                    toast.update(toastID, { render: "Collection already exists.", type: "error", isLoading: false, autoClose: 3000 })
                    self.setState({ isRequesting: false });
                }
                else {
                    this.onCreateLoad(toastID)
                }
            }
            else this.onCreateLoad(toastID)
        }
    }
    onCreateLoad = (toastID) => {
        var self = this
        const { collectioName } = this.state;
        createCollection({ name: collectioName }).then((response) => {
            self.setState({ isRequesting: false })
            toast.update(toastID, { render: "Collection has been successfully created.", type: "success", isLoading: false, autoClose: 3000 });
            self.props.addCollectionCallbackWithAPI(false)
            getCollectionList().then((response) => {
                const { responseData } = response.data;
                if (responseData && Object.keys(responseData).length > 0) {
                    self.props.dispatch({ type: "COLLECTIONS", payload: { collection: responseData.collection, assetsCount: responseData.assets } })
                }
            })

        }).catch((ex) => {
            self.setState({ isRequesting: false })
        })
    }
    onChangeCollectionName = event => {
        this.setState({ collectioName: event.target.value })
    }

    handleClose = event => this.setState({ collectioName: "" }, () => this.props.addCollectionCallback(false))


    render() {
        const { addCollectionModal } = this.props
        const { collectioName, isRequesting } = this.state
        return <Modal show={addCollectionModal} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Create New Collection</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-3 text-muted mb-2">Collection Name</label>
                        <div className="col-sm-9">
                            <input type="text" name="company" className="form-control" placeholder="Enter collection name..." onChange={this.onChangeCollectionName} value={collectioName} onKeyUp={this.onEnterCreate} />
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className={`btn btn-primary btn-sm btn-min-w${isRequesting ? ` spinner disabled` : ``}`} onClick={this.onClickCreate}>Create</button>
                </div>
            </Modal.Footer>
        </Modal>
    }
}
const mapStateToProps = (state) => {
    return {
        collectionData: state.collection
    }
}
export default connect(mapStateToProps)(CreateCollectionModal);
